.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 3s linear;
          animation: App-logo-spin infinite 3s linear;
  height: 80px;
}

.App-header {
  background-color: white;
  height: 150px;
  padding: 20px;
  color: black;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  20% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  40% { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  60%{ -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  80%{ -webkit-transform: rotate(-180deg); transform: rotate(-180deg); }
}

@keyframes App-logo-spin {
  20% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  40% { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
  60%{ -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  80%{ -webkit-transform: rotate(-180deg); transform: rotate(-180deg); }
}



